const homeMap = document.getElementById('homeMap');
const scrollContinentes = document.querySelector('.scroll-continentes');

const scrollEuropa = scrollContinentes.querySelector('#scroll-europa');
const scrollSudamerica = scrollContinentes.querySelector('#scroll-sudamerica');
const scrollCentroamerica = scrollContinentes.querySelector('#scroll-centroamerica');
const scrollNorteamerica = scrollContinentes.querySelector('#scroll-norteamerica');
const scrollAsia = scrollContinentes.querySelector('#scroll-asia');
const scrollAfrica = scrollContinentes.querySelector('#scroll-africa');
const scrollCaribe = scrollContinentes.querySelector('#scroll-caribe');
const scrollOceania = scrollContinentes.querySelector('#scroll-oceania');

let api_key = 'AIzaSyAWMWZNZ8MpkhD1qGREkRwR-IbuA17VzzE';

/* 
--place: displays a map pin at a particular place or address, such as a landmark, business, geographic feature, or town.
--view: returns a map with no markers or directions.
--directions: displays the path between two or more specified points on the map, as well as the distance and travel time.
--streetview: shows interactive panoramic views from designated locations.
--search: shows results for a search across the visible map region. */

/* 
WOLD: q=zoom:0
OCEANIA: q=place_id:ChIJQbA4_Cu8QW4RbuvrxISzaks 
*/

let ubications = {
	continentes: {
		europa: 'zoom=3&center=54.5260,15.2551',
		sudamerica: 'zoom=2&center=-8.7832%2C-55.4915',
		centroamerica: 'zoom=5&center=12.7690%2C-85.6024',
		norteamerica: 'zoom=2&center=54.5260%2C-105.2551',
		asia: 'zoom=2&center=34.0479%2C100.6197',
		africa: 'zoom=2&center=-8.7832%2C34.5085',
		oceania: 'zoom=3&center=-22.7359%2C140.0188',
		caribe: 'zoom=4&center=21.4691%2C-78.6569'
	}
};

let map_mode = 'view';
let parameters = '';

scrollContinentes.addEventListener('click', function (e) {
	if (e.target && e.target !== scrollContinentes) {
		switch (e.target) {
			case scrollEuropa:
            parameters = ubications['continentes']['europa'];
				break;
			case scrollSudamerica:
            parameters = ubications['continentes']['sudamerica'];
				break;
			case scrollCentroamerica:
            parameters = ubications['continentes']['centroamerica'];
				break;
			case scrollNorteamerica:
            parameters = ubications['continentes']['norteamerica'];
				break;
			case scrollAsia:
            parameters = ubications['continentes']['asia'];
				break;
			case scrollAfrica:
            parameters = ubications['continentes']['africa'];
				break;
			case scrollOceania:
            parameters = ubications['continentes']['oceania'];
				break;
			case scrollCaribe:
            parameters = ubications['continentes']['caribe'];
				break;
		}

		let src = `https://www.google.com/maps/embed/v1/${map_mode}?${parameters}&key=${api_key}`;
		homeMap.setAttribute('src', src);
	}
});
